import { createMuiTheme } from '@material-ui/core/styles'

import Settings from './Settings'

const colors = {
	red: require('@material-ui/core/colors/red').default,
	pink: require('@material-ui/core/colors/pink').default,
	purple: require('@material-ui/core/colors/purple').default,

	deepPurple: require('@material-ui/core/colors/deepPurple').default,
	indigo: require('@material-ui/core/colors/indigo').default,
	blue: require('@material-ui/core/colors/blue').default,

	lightBlue: require('@material-ui/core/colors/lightBlue').default,
	cyan: require('@material-ui/core/colors/cyan').default,
	teal: require('@material-ui/core/colors/teal').default,

	green: require('@material-ui/core/colors/green').default,
	lightGreen: require('@material-ui/core/colors/lightGreen').default,
	lime: require('@material-ui/core/colors/lime').default,

	yellow: require('@material-ui/core/colors/yellow').default,
	amber: require('@material-ui/core/colors/amber').default,
	orange: require('@material-ui/core/colors/orange').default,

	deepOrange: require('@material-ui/core/colors/deepOrange').default,
	brown: require('@material-ui/core/colors/brown').default,
	grey: require('@material-ui/core/colors/grey').default,

	blueGrey: require('@material-ui/core/colors/blueGrey').default
};


let themeColors = {
	'primary': colors.blue[700],
	'secondary': colors.pink[500]
};

const userTheme = {
	'primary': Settings.getSetting('themeColorPrimary'),
	'secondary': Settings.getSetting('themeColorSecondary')
};

['primary', 'secondary'].forEach(key => {
	if (userTheme && userTheme[key]) {
		if (userTheme[key].indexOf('|') > -1) {
			let [ hue, shade ] = userTheme[key].split('|');
			themeColors[key] = (colors[hue] && colors[hue][shade]) || themeColors[key];
		}
		else {
			themeColors[key] = userTheme[key];
		}
	}
});


const theme = createMuiTheme({
	palette: {
		primary: {
			main: themeColors.primary 
		},
		secondary: {
			main: themeColors.secondary 
		}
	},
	components: {
		icon: {
			fontSize: '24px',
			color: themeColors.primary
		},
		'finger': {
			cursor: 'pointer'
		}
	},
	...userTheme
});

export default theme;